  /* +++++++++++++++++++++++++++++++++++++++++++++++++++
  .Res_But */
  .Res_But {
    font-weight: bold;
    color: white;
    background-color: #1b164a;
    padding: 1em 2em;
    border:3px solid #ff256d;
    border-radius: .6rem;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    margin-top: 50px;
    width: 100px;
  }

  .Resumebut{
    position: relative;
    text-decoration: none;
    color: white;
  }
  
  .Res_But span:not(:nth-child(6)) {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    height: 30px;
    width: 30px;
    background-color: #ff256d;
    border-radius: 50%;
    transition: .6s ease;
  }
  
  .Res_But span:nth-child(6) {
    position: relative;
  }
  
  .Res_But span:nth-child(1) {
    transform: translate(-3.3em, -4em);
  }
  
  .Res_But span:nth-child(2) {
    transform: translate(-6em, 1.3em);
  }
  
  .Res_But span:nth-child(3) {
    transform: translate(-.2em, 1.8em);
  }
  
  .Res_But span:nth-child(4) {
    transform: translate(3.5em, 1.4em);
  }
  
  .Res_But span:nth-child(5) {
    transform: translate(3.5em, -3.8em);
  }
  
  .Res_But:hover span:not(:nth-child(6)) {
    transform: translate(-50%, -50%) scale(4);
    transition: 1.5s ease;
  }
  /* +++++++++++++++++++++++++++++++++++++++++++++++++++++ arrow  */
  
  #arrowAnim {
    width:0px;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* margin-right:500px; */
  }
  
  .arrow {
    /* width: 5px;
    height: 5px; */
    border: 2.5vw solid;
    border-color: #ff256d transparent transparent #ff256d;
    transform: rotate(-45deg);
  }
  .arrowSliding {
    position: absolute;
    -webkit-animation: slide 4s linear infinite; 
            animation: slide 4s linear infinite;
  }
  
  .delay1 {
    -webkit-animation-delay: 1s; 
      animation-delay: 1s;
  }
  .delay2 {
    -webkit-animation-delay: 2s; 
      animation-delay: 2s;
  }
  .delay3 {
    -webkit-animation-delay: 3s; 
      animation-delay: 3s;
  }
  
  @-webkit-keyframes slide {
      0% { opacity:0; transform: translateX(15vw); }  
     20% { opacity:1; transform: translateX(9vw); } 
     80% { opacity:1; transform: translateX(-9vw); }  
    100% { opacity:0; transform: translateX(-15vw); } 
  }
  @keyframes slide {
      0% { opacity:0; transform: translateX(15vw); }  
     20% { opacity:1; transform: translateX(9vw); } 
     80% { opacity:1; transform: translateX(-9vw); }  
    100% { opacity:0; transform: translateX(-15vw); } 
  }

