  .flip-card {
    background-color: transparent;
    width: 254px;
    height: 254px;
    perspective: 1000px;
  }
  
  .title {
    font-size: 1.5em;
    font-weight: 900;
    text-align: center;
    margin: 0;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
  }
  
  .flip-card-front, .flip-card-back {
    box-shadow: 0 8px 14px 0 rgba(0,0,0,0.2);
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 20px;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border: 1px solid coral;
    border-radius: 1rem;
  }
  
  .flip-card-front {
    background: linear-gradient(120deg, #ff256d 40%, rgb(255, 222, 241) 88%,
    #ff256d 40%, #ff256eac 48%);
    color: #ff256d;
  }
  
  .flip-card-front img{
    height: 80%;
    width: 80%;
  }
  
  .flip-card-back {
    background: linear-gradient(120deg, #ff95b8 30%, #ff256d 88%,
    #ff256d 40%, #faa3c0 78%);
    color: white;
    transform: rotateY(180deg);
  }


