  /* ++++++++++++++++++++++++++++++++++++++++++++++++ Project CSS */

  .card {
    height: 450px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap:40px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    padding:0px 32px 32px 32px;
    overflow: hidden;
    border-radius: 10px;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
  }
  
  .content {
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* gap: 20px; */
    color: #1b164a;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
  }
  
  .content .heading {
    margin-bottom:7px;
    font-weight: 700;
    font-size: 32px;
  }
  
  .content .para {
    margin-bottom:10px;
    line-height: 1.5;
  }
  
  .content .btn {
    color: #e8e8e8;
    text-decoration: none;
    padding: 10px;
    font-weight: 600;
    border: none;
    cursor: pointer;
    background: linear-gradient(-45deg,#1b164a 0%, #ff256d 100% );
    border-radius: 5px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  }
  
  .card::before {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 5px;
    height: 100%;
    background: linear-gradient(-45deg, #1b164a 0%, #ff256d 100% );
    z-index: -1;
    transition: all 0.5s cubic-bezier(0.23, 1, 0.320, 1);
  }
  
  .card:hover::before {
    width: 100%;
  }
  
  .card:hover .upon_Card{
    box-shadow: rgba(255, 255, 255, 0.55) 0px 7px 29px 0px;
  }
  
  .card:hover .content{
    color: #e8e8e8;
  }

  .card:hover {
    color: #e8e8e8;
  }
  
  .card:hover .heading{
    color:white;
  }
  
  .card:hover .btn {
    color: #212121;
    background: #e8e8e8;
  }
  
  .content .btn:hover{
    outline: 2px solid #e8e8e8;
    background: transparent;
    color: #e8e8e8;
  }
  
  .content .btn:active {
    box-shadow: none;
  }
  
